@import "./abstracts/vars";
@import "./abstracts/mixins";


*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html,
body {
    font-family: 'Montserrat', sans-serif;
    min-width: 320px;
}

@import "./header";
@import "./main-block";
@import "./photosession";
@import "./rating";
@import "./photos";
@import "./about";
@import "./photografer";
@import "./products";
@import "./feedback";
@import "./footer";


.container {
    @extend %container;
}